import React from 'react';
import { useState } from 'react';
import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import Textform from './components/Textform';
import Alert from './components/Alert';

// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
//   Link
// } from "react-router-dom";


function App() {
  const [mode,setMode]=useState('light') //weather dark mode in enabled or not
  const[alert,setAlert]=useState('null')

  const showAlert=(message,type)=>
  {
    setAlert({
      msg:message,
      type:type

    })
    setTimeout(()=>{
      setAlert(null);
    },3000);
  }
 const tooglemode=()=>{
    if(mode ==='light')
    {
      setMode('dark');
      document.body.style.backgroundColor ='#042743';
      showAlert("dark mode is activated","success : ")
      document.title='textUtil-darkMode';
    }
    else{
      setMode('light');
      document.body.style.backgroundColor ='white';
      showAlert("light mode is activated","success : ")
      document.title='textUtil-lightMode';


    }
  }

  return (
    <>
    {/* <Router> */}
    <Navbar title="text utils" mode={mode} tooglemode={tooglemode}/>
    <Alert alert={alert}/>
    <div className="container  my-3">
    {/* <Routes> */}
          {/* <Route exact path="/about"> */}
            {/* <About/> */}
          {/* </Route> */}
          
          {/* <Route exact path="/"> */}
            <Textform showAlert={showAlert} heading="Enter The Text Which Want To Convert" mode={mode} />
            {/* </Route> */}
        {/* </Routes> */}
    </div>
        {/* </Router> */}
      
     
    </>
  );
  
}

export default App;
