import React,{useState} from 'react'

export default function Textform(props) {
    let handleupclick =()=>{
        // console.log("button is clicked"+ text);
        let newText=text.toUpperCase();
        setText(newText)
        props.showAlert("converted to Upper case","success : ")

    }

    let handleloclick =()=>{
      // console.log("button is clicked"+ text);
      let newText=text.toLowerCase();
      setText(newText)
      props.showAlert("converted to Lower case","success : ")
  }
  let clearText =()=>{
    //  console.log("button is clicked"+ text);
    let newText="";
    setText(newText)
    props.showAlert("Text cleared","success : ")

}


    let handleOnChange =(event)=>{
        // console.log("button is changed");
        setText(event.target.value)
    }

    const [text, setText] = useState('');
    // setText("kgyy");
    
  return (
    <>
    <div className="container" style={{color:props.mode==='dark'?'white':'black'}}>
        <h1>{props.heading}</h1>
      <div className="mb-3" style={{color:props.mode==='dark'?'white':'black'}}>
          <textarea className="form-control" style={{backgroundColor:props.mode==='dark'?'grey':'white'}} placeholder="enter your text here" id="exampleFormControlTextarea1" value={text} onChange={handleOnChange} rows="8"></textarea>
        </div>
        <button className="btn btn-primary mx-2" onClick={handleupclick}>Convert To UpperCase</button>
        <button className="btn btn-primary mx-2" onClick={handleloclick}>Convert To lowerCase</button>
        <button className="btn btn-primary mx-2" onClick={clearText}>clear text</button>

    </div>
    <div className="container my-3" style={{color:props.mode==='dark'?'white':'black'}}>
      <h1>Your text summary</h1>
      <p>{text.split(" ").length} charcters and {text.length} words</p>
      <p>Take {0.008 * text.split(" ").length} Minutes to read these words</p>
      <h3>Preview</h3>
      <p>{text}</p>
    </div>
    </>
  
  )}


